:root {
  //Primary colors
  --dark-blue: hsl(233, 47%, 7%);
  --desaturated-blue: hsl(244, 38%, 16%);
  --soft-voilet: hsl(277, 64%, 61%);

  //Neutral colors
  --white: hsl(0, 0%, 100%);
  --main-transparent-white: hsla(0, 0%, 100%, 0.75);
  --stat-transparent-white: hsla(0, 0%, 100%, 0.6);
}
